/********** slider Input Styles **********/
/*slider Reset*/
.passwordGenerator_slider__17G6D {
    align-self: center;
    width: 100%;
    height: 10px;
    border-radius: 5px;
    -webkit-appearance: none;
     appearance: none;
     background: transparent;
     cursor: pointer;
 }

 /* slider track */
 .passwordGenerator_slider__17G6D::-webkit-slider-runnable-track {
    background-color: #053a5f;
    border-radius: 0.5rem;
    height: 0.5rem;  
 }
 
 /* slider thumb */
 .passwordGenerator_slider__17G6D::-webkit-slider-thumb {
    -webkit-appearance: none;
            appearance: none;
    margin-top: -10px; 
    box-shadow: 5px 3px 11px 1px rgba(0,0,0,0.79);
    background-color: #5cd5eb;
    width: 25px;
    height: 25px;
    border-radius: 25px;
 }

 /********** Toggle Switch Styles **********/
.passwordGenerator_input_toogle__3I0dJ {
    margin-top: 20%;
    -webkit-appearance: none;
    width: 65px;
    height: 30px;
    border-radius:25px;
    background-color: #5cd5eb;
    position: relative;
    cursor: pointer;
    outline: none;
}

.passwordGenerator_input_toogle__3I0dJ::after {
    width: 25px;
    height: 25px;
    background-color: white;
    box-shadow: 5px 3px 11px 1px rgba(0,0,0,0.79);
    position: absolute;
    content: "";
    top: 2.5px;
    left: 5px;
    border-radius: 50%;
    z-index: 3;
}

.passwordGenerator_input_toogle__3I0dJ:checked {
    background-color: #053a5f;
}

.passwordGenerator_input_toogle__3I0dJ:checked::after {
    left: 34px;
}

.passwordGenerator_toogle_wrap__3oHJk {
    position: relative;
}

.passwordGenerator_on_off__3LHJr {
    color: white;
    position: absolute;
    top: -7px;
    font-size: 13px;
    z-index: 1;
    cursor: pointer;
}

.passwordGenerator_on__3kAJv {
